.navbar2 {
  position: sticky;
  top: 0px;
  height: 70px;
  background-color: rgb(0, 0, 0);
  opacity: 0.82;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px 0px 30px;
  margin-bottom: -70px;
  z-index: 9999;
  overflow: hidden;
  margin-bottom: 0px;
  font-family: 'Varela Round';
}

.navbar__logo {
  color: white;
  font-family: 'MonteCarlo', cursive;
  font-size: 2.5rem;
}

.navbar__list {
  display: flex;
  list-style: none;
}

.navbar__item {
  white-space: nowrap;
  margin-top: 5px;
  cursor:pointer;
}

.navbar__link {
  /* font-size: 20px; */
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-align: center;
  /* padding-right: 5px; */
  margin: 0px 5px;
  border-style: solid;
  /* border-width: 0px 0px 1px 0px; */
  border-color: transparent;
  transition: 250ms;
}

.navbar__link :hover {
  border-color: white;
  transition: 250ms;
}

.navbar__menu {
  color: white;
  display: none;
  cursor: pointer;
}

@media screen and (max-width:1350px) {
  .navbar__list {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    top: 70px;
    left: -100%;
    width: 100%;
    height: 300px;
    position: fixed;
    transition: all 0.5s;
  }
  .navbar__list--active{
    left:0;
    color:yellow;
  }
  .navbar__item{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    flex: 1;
    color:yellow;
  }
  .navbar__link{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0px;
    border-width: 0px;
    background-color: black;
    opacity: 0.75;
    color:#89FFFD;
    font-size: 32px;
  }
  .navbar__link :hover{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0px;
    border-width: 0px;
    background-color: #ef32d9a3;
    opacity: 0.8;
    color: #89FFFD;
    font-size: 32px;
  }
  .navbar__menu {
    display: block;
    color:yellow;
  }
}