.auth-form-container2 {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  background-image: #1e1e1e;
  padding: 5rem;
  width: 75vw;
  margin-left: 0;
  margin-top:-1rem;
  /* box-shadow: inset 20px 2px 20px 0px #E0241E; */
  /* clip-path: inset(Rpx); */
}

.register_container {
  width: 600px !important;
  position: relative;
  margin: 0 auto;

}

.logo-register{
  float: right;
  width: 40vw;
  height: 100vh;
  height: 90vh;
  margin: 10px 13px 0px 100px;
}

.register_image {
  width: 100%;
  height: 100%;
  float: left;
}


.register__page {
  display: flex;
}

.register-content {
  background: #ffffff;
  border-radius: 10px;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  -o-border-radius: 10px;
  -ms-border-radius: 10px;
  padding: 29px 85px;
  background:  #84d3eb;
  display: flex;
  flex-direction: column;
  font-family: 'Exo'
}

.form-inputt {
  border-radius: 25px;
  padding: 20px 25px;
  width: 415px;
  height: 55px;
  font-size: 20px;
}


label {
  text-align: right;
  padding: 0.25rem 0;

}

input {
  margin: 0.5rem 0;
  padding: 1rem;
  border: yellow;
  border-radius: 10px;
}
.register-form{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}

.register-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 45px;
  padding: 10px;
  color: white;
  font-weight: 900;
  background-color: #b70000;
  border: #DC0000;
  width: 178px;
  font-size: 25px;
  cursor: pointer;
  margin-top: 30px;

}
.register-signup{
  align-items: center;
  justify-content: center;
  border-radius: 45px;
  padding: 10px;
  color: white;
  font-weight: 900;
  background-color: #ef32d9;
  border: #ef32d9;
  width: 178px;
  font-size: 25px;
  cursor: pointer;
  margin-top: 30px;
}
.link-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 45px;
  padding: 10px;
  color: white;
  font-weight: 900;
  background-color: #ef32d9;
  border: #ef32d9;
  width: 178px;
  font-size: 25px;
  cursor: pointer;
  margin-top: 30px;
}

.label-register{
  font-size: 22px;
  text-align:left;
}

.link-btn :hover{
  color: #6c757d;
  font-weight: 500;
  text-align: center;
  margin-top: 91px;
  margin-bottom: 5px;
}

